@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none}

html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}a{text-decoration:none;color:inherit;cursor:pointer}a:hover{color:none}button{background-color:transparent;color:inherit;border-width:0;padding:0;cursor:pointer}figure{margin:0}input::-moz-focus-inner{border:0;padding:0;margin:0}ul,ol,dd{margin:0;padding:0;list-style:none}h1,h2,h3,h4,h5,h6{margin:0;font-size:inherit;font-weight:inherit}p{margin:0}cite{font-style:normal}fieldset{border-width:0;padding:0;margin:0}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-family:'Roboto', sans-serif;font-weight:400;line-height:1.3;color:black}h1{font-size:32px}p{font-size:24px}.fixed{width:100%;position:fixed;top:0;right:0;left:0;z-index:1030;background-color:white}section{padding-bottom:20px;padding-top:20px}#about>.about_content{padding:20px;max-width:800px;margin:auto;font-weight:600}#about>.about_content>h1.about_title{text-align:center;color:rgba(45,121,219,0.973)}#about>.about_content>h1{font-size:4rem;color:rgba(40,152,218,0.856)}#contact{padding-top:20px}@media (min-width: 587px){#contact{padding-top:100px}}#contact .social_links{display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr))}#contact .social_link{padding:10px;border:1px solid white;text-align:center}#contact .social_link>a{text-decoration:none;color:#3891f7}#projects{padding:20px 10px;padding-bottom:80px;background-color:rgba(224,254,255,0.856)}#projects>h1{padding:40px;text-transform:uppercase;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;font-size:3rem;margin:auto}#projects .gallery{display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));grid-template-columns:1fr 1fr}@media (max-width: 587px){#projects .gallery{grid-template-columns:1fr}}#projects .gallery img{width:100%;height:auto}.project{width:100%;overflow:hidden;display:grid;grid-template-columns:1 / -1;grid-template-rows:1 / -1}.project img{grid-column:1 / -1;grid-row:1 / -1;width:100%;height:100%;object-fit:cover}.project__overlay{background-color:rgba(0,0,0,0.8);grid-column:1 / -1;grid-row:1 / -1;position:relative;display:flex;text-align:center;flex-direction:column;justify-content:center;align-items:center;transition:0.4s;transform:translateY(-100%);color:white}.project__overlay>h3{font-size:32px}.project__overlay>h3 a:hover{color:inherit;text-decoration:none;cursor:pointer}.project__overlay>p{padding:5px;color:rgba(45,121,219,0.973)}.project:hover .project__overlay{transform:translateY(0%)}.navbar-toggler-icon{color:red}.links .link-box{padding:10px 0px}.links a{width:100%;margin:20px;transition:0.3s}@media (max-width: 460px){.links a{margin:0}}.links a:hover,.links a.active{color:#184bd8}

